/*
  CSS Reset and Base Styles
  This file contains a set of base styles to ensure consistency across browsers.
  It includes box-sizing, margin resets, and other foundational styles.
*/

/* 1. Use a more-intuitive box-sizing model */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* 2. Remove default margin */
* {
  margin: 0;
}

/* 3. Enable keyword animations */
@media (prefers-reduced-motion: no-preference) {
  html {
    interpolate-size: allow-keywords;
  }
}

body {
  /* 4. Add accessible line-height */
  line-height: 1.5;
  /* 5. Improve text rendering */
  -webkit-font-smoothing: antialiased;
}

/* 6. Improve media defaults */
img,
picture,
video,
canvas,
svg {
  display: block;
  max-width: 100%;
}

/* 7. Inherit fonts for form controls */
input,
button,
textarea,
select {
  font: inherit;
}

/* 8. Avoid text overflows */
p,
h1,
h2,
h3,
h4,
h5,
h6 {
  overflow-wrap: break-word;
}

/* 9. Improve line wrapping */
p {
  text-wrap: pretty;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  text-wrap: balance;
}

/*10. Create a root stacking context*/
#root,
#__next {
  isolation: isolate;
}

/*11. Remove list styles for ul*/
ul {
  list-style: none;
  padding: 0; /* Ensure no padding on the left */
  margin: 0; /* Ensure no margin */
}

/*12. Remove link styles for a tags*/
a {
  text-decoration: none; /* Remove underline from links */
  color: inherit; /* Inherit color from parent */
}

/*13. Smooth scrolling for in-page links when supported */
@media (prefers-reduced-motion: no-preference) {
  :has(:target) {
    scroll-behavior: smooth;
    scroll-padding-top: 10rem;
  }
}

/*14. Smooth scrolling for the entire document*/
html {
  scroll-behavior: smooth; /* Enable smooth scrolling for the entire document */
}
